.social {

  &__list {
    .ul-list();
    width: 151px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin:0 auto;
    margin-bottom: 38px;
  }

  &__item {

    background-color: @bg-blue;

    &:hover {
      background-color: @bg-blue-hover;
    }

    &:active {
      background-color: @bg-blue-active;
      .social__icon{
      fill: rgba(255, 255, 255, 0.3);
      }
    }


  }

  &__icon {
    fill: #ffffff;
  }

  &__link {
    display: flex;
    width: 46px;
    height: 48px;
    justify-content: center;
    align-items: center;
  }
}

// tablet version

@media (min-width: @tablet-width) {

  .social__list {
    margin-bottom: 0;
  }
}
