.page-footer {
  padding-bottom: 80px;
  .mobile-page-style();
  text-align: center;
  &__title {
    margin: 0;
    .font-values(21px; 26px);
    font-weight: bold;
    margin-top: 28px;
    margin-bottom: 38px;
  }
}

// tablet version

@media (min-width: @tablet-width) {
  .page-footer {
    width: 648px;
    display: flex;
    justify-content: space-between;

    &__social {
      display: flex;
      align-items: center;
    }
 }
}

// desktop version

@media(min-width: @desktop-width) {
  .page-footer {
    width: 960px;
  }
}

