@keyframes slideIn {
  0% {
   margin-top: -200px;
  }
  100% {
    margin-top: 0;
  }
}

@keyframes slideOut {
  0% {
   margin-top: 200px;
  }
  100% {
    margin-top: 0;
  }
}

.main-nav {

  &__list {
    position: relative;
    .ul-list();
    color: @bg-white;
    padding-bottom: 55px;
  }

  &--opened {
    animation-duration: 0.3s;
    animation-name: slideIn;
  }

  &--closed {
    animation-duration: 0.3s;
    animation-name: slideOut;
  }

  &__logo-image {

    position: absolute;
    bottom: -28px;
    left: 34.6%;
    z-index: 1;
  }

  &__toggle {
    position: absolute;
    right: 15px;
    top: 16px;
    width: 30px;
    height: 30px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    z-index: 30;
  }
}

 .main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 1px;
  right: 5px;
  width: 23px;
  height: 4px;
  background-color: @bg-blue;
  box-shadow: 0 8px 0 0 @bg-blue, 0 16px 0 0 @bg-blue;

}

.main-nav--closed .main-nav__toggle:active::before{
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.3), 0 16px 0 0 rgba(129, 179, 210, 0.3);
}

.main-nav--closed .main-nav__toggle:hover::before {
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.6), 0 16px 0 0 rgba(129, 179, 210, 0.6);
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 10px;
    left: 0;

    width: 28px;
    height: 4px;

    background-color: #ffffff;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }

  &:hover::before,
  &:hover::after {
    background-color: rgba(255, 255, 255, 0.6);
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

@media (max-width: 767px){
.main-nav--closed .site-list__item:not(.site-list__item-logo) {
 display: none;
}
}

// tablet version

@media (min-width: @tablet-width){
  .main-nav{

  &__list {
  padding-bottom: 0px;
 }

  &__toggle {
    display: none;
  }

  &__logo-image {
    left: 43.4%;
  }
}
}

// desktop version

@media (min-width: @desktop-width){
  .main-nav{
    &__logo-image {
      left: 42%;
      top: 0px;
      width: 187px;
      height: 96px;
    }
  }
}
