
.presentation {
  text-align: center;
  margin-bottom: 140px;

  &__title {
margin-bottom: 30px;
 }

  &__text {
 padding-left: 43px;
 padding-right: 41px;
 margin-bottom: 33px;
  }

  &__video-player {
    width: 100%;
    min-height: 201px;
    background-image: url(/img/video-mobile@1x.jpg);

    @media (min-resolution: @retina-dppx) {
      background-image: url(/img/video-mobile@2x.jpg);
      background-size: auto 201px;

    }
  }
}



// tablet-version

@media (min-width: @tablet-width) {

  .presentation {
    padding: 0 60px;
    margin-bottom: 55px;

    &__title {
      .font-values(30px;26px);
      margin-bottom: 31px;
    }

    &__text {
      margin-bottom: 73px;
      padding: 0px 118px 0 111px;
    }

    &__video-player {
      min-height: 500px;
      background-image: url("/img/video-tablet@1x.jpg");
      background-repeat: no-repeat;


      @media (min-resolution: @retina-dppx) {
        background-image: url(/img/video-tablet@2x.jpg);
        background-size: auto 500px;
   }
  }
 }
}


// desktop version

@media (min-width: @desktop-width) {
  .presentation {
    padding: 0 200px;
    margin-bottom: 100px;

    &__text{
      padding: 0px 191px 0 190px;
    }


    &__video-player {
      background-image: url("/img/video-desktop@1x.jpg");

      @media (min-resolution: @retina-dppx) {
        background-image: url(/img/video-desktop@2x.jpg);

   }
  }
 }
}
