.modal-success {
position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-family: inherit;
  z-index: 31;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease 0s;


}

.modal-open {
  visibility: visible;
  opacity: 1;

}

.modal-open .modal-body{
  transform: translate(0, 0);
  opacity: 1;
}


// tablet version

@media (min-width: @tablet-width) {
  .modal-success {

   & &__content--big {
      width: 640px;
    }

  }
  }

