.form {


   &__title {

    text-align: center;
    margin-bottom: 27px;
  }

   &__contents {
      padding-bottom: 16px;
      margin-bottom: 18px;
      text-align: center;
      border-bottom: 1px solid #e5e5e5;

    }

   &__full-name {
     margin: 0;
     padding: 0;
     margin-bottom: 17px;
     padding-bottom: 5px;
     border: none;
     border-bottom: 1px solid #e5e5e5;

   }

   &__full-name-title {
     text-align: center;
     margin-bottom: 22px;
   }

   &__full-name-label {
     display: flex;
    align-items: center;
    padding: 0 20px;
    margin-bottom: 20px;
    font-kerning: none;

   }

   &__input {
    font-family: inherit;
    .font-values(18px; 26px);
    border: none;
    width: 100%;
    background-color: #f2f2f2;
    padding: 21px;
    margin-left: 15px;
    font-weight: bold;
    height: 58px;
    box-sizing: border-box;

      &::placeholder {
        color: #000000;
      }

      &:hover {
        background-color: #e5e5e5;
        opacity: 1;
        transition: opacity 0.3s ease;
      }

      &:focus {
        border: 4px solid #e5e5e5;
        background-color: #ffffff;
        outline: none;

        &::placeholder {
          opacity: 0;
          transition: opacity 0.3s ease;
        }
      }
   }



   &__contacts {
     border: none;
   }

   &__input--error {
    border: 4px solid #ff0000;
    padding-left: 17px;
   }

&__contacts {
  margin: 0;
  padding: 0;
  border: none;
  border-bottom: 1px solid #e5e5e5;
  margin-bottom: 17px;
  padding-bottom: 14px;

  }

 &__contacts-title {
   position: relative;
    text-align: center;
    margin-bottom: 20px;
  }

 &__contacts-label {
   display:flex;
   flex-wrap: wrap;
   padding: 0 20px;
   margin-bottom: 12px;
 }

 &__contacts-text {
   margin-bottom: 10px;
   margin-left: 22px;
 }


 &__input--contacts {
  width: 50%;
  flex-grow: 1;
  margin-left: 0;
 }

 &__icon-phone {
   background-image: url("/img/icon-phone.svg");
   background-repeat: no-repeat;
   background-color: #e6e6e6;
   background-position: center;
   width: 58px;
   height: 58px;
 }

 &__icon-message {
  background-image: url("/img/icon-mail.svg");
  background-repeat: no-repeat;
  background-color: #e6e6e6;
  background-position: center;
  width: 58px;
  height: 58px;
 }

 &__impression {
   border: none;
   margin: 0;
   padding: 0;
   border-bottom: 1px solid #e5e5e5;
   padding-bottom: 12px;
   margin-bottom: 20px;

 }

 &__impression-title {
   text-align: center;
   margin-bottom: 20px;
 }

 &__impression-label {
   display: flex;
   align-items: center;
   position: relative;
   margin-bottom: 15px;
   padding: 0 20px;
   text-transform: none;

     &::before {
        content: "";
        flex: 0 0 55px;
        height: 55px;
        background-color: #f2f2f2;
        border-radius: 50%;
        align-self: flex-start;
        margin-right: 23px;
      }

     &::after {
        position: absolute;
        content: "";
        width: 20px;
        height: 20px;
        background-color: #333333;
        border-radius: 50%;
        top: 18px;
        left: 38px;
        transform: scale(0);
        transition: transform 0.3s ease 0s;
     }
    }

    &__impression-text {
      .font-values(18px;26px);
      font-weight: bold;
    }

&__input--radio:checked + &__impression-label::after {
   transform: scale(1);
}

&__visited-attractions {
  border: none;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #e5e5e5;
  margin-bottom: 17px;

}

&__visited-attractions-title {
  text-align: center;
  margin-bottom: 20px;
}

&__visited-attractions-label {
  text-transform: none;
  padding: 0 20px;
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 25px;

  &::before {
    content: "";
    flex: 0 0 55px;
    height: 55px;
    background-color: #f2f2f2;
    border-radius: 5px;
    margin-right: 22px;
  }

  &::after {
    position: absolute;
    content: "";
    width: 24px;
    height: 16px;
    background-image: url(/img/icon-tick.svg);
    background-repeat: no-repeat;
    top: 19px;
    left: 37px;
    transform: scale(0);
    transition: transform 0.3s ease 0s;
  }
}

&__input--checkbox:checked + &__visited-attractions-label::after {
  transform: scale(1);
}


&__feeling {
  border: none;
  margin: 0;
  padding: 0 20px;
  padding-bottom: 12px;
  margin-bottom: 33px;
}

&__feeling-title {
  text-align: center;
  margin-bottom: 20px;

}

&__feeling-review {
  font-family: inherit;
  width: 100%;
  text-transform: none;
  background-color: #f2f2f2;
  min-height: 160px;
  padding: 15px 20px;
  box-sizing: border-box;
  border: none;
}

&__button-wrap {
  padding: 0 20px;
}

&__button {
  font-family: inherit;
  width: 100%;
  padding: 15px 48px;
}

&__footnote {
  display: none;
}

}


// tablet-version

@media (min-width: @tablet-width) {

  .form {
    margin-bottom: 80px;

    &__title {
      margin-bottom: 30px;
    }

    &__contents {
      padding-left: 160px;
      padding-right: 160px;
      border-bottom: none;
      margin-bottom: 57px;
    }

    &__fieldset-wrap {
      display: flex;
      flex-wrap: wrap;
      padding: 0 60px;
    }

    &__full-name {
      width: 50%;
      order: 2;
      border-bottom: none;
    }

    &__full-name-title {
      padding-left: 57px;
      text-align: start;
      margin-bottom: 60px;
    }

    &__full-name-label {
      padding: 0;
      padding-left: 58px;
    }

    &__contacts {
      margin-bottom: 13px;
      width: 100%;
      order: 3;
      border-bottom: none;
    }

    &__contacts-title {
      margin-bottom: 27px;

      &::before {
        content: "";
        position: absolute;
        width: 125px;
        height: 1px;
        background-color: #e5e5e5;
        top: 13px;
        left: -182px;
        }


        &::after {
          content: "";
          position: absolute;
          width: 125px;
          height: 1px;
          background-color: #e5e5e5;
          top: 13px;
          right: -182px;
          }
      }

    &__contacts-text {
      width: 100%;
      margin-bottom: 7px;
    }

    &__contacts-label {
      margin-bottom: 25px;
      padding: 0;
    }

    &__impression {
      position: relative;
      width: 50%;
      order: 1;
      border-bottom: none;
      margin-bottom: 37px;

        &::before {
          content: "";
          position: absolute;
          height: 292px;
          width: 1px;
          right: 0;
          top: 6px;
          background-color:  #e5e5e5;
        }
    }


    &__impression-title {
      margin-bottom: 59px;
      padding: 0;
      text-align: start;
   }

   &__impression-label {
      margin-bottom: 23px;
      padding: 0;

      &::after {
        top: 18px;
        left: 18px;
      }
   }

    &__impression-text {
      .font-values(18px;26px);
    }

    // visited-attractions
    //   :

    &__visited-attractions {
      width: 100%;
      order: 4;
      border-bottom: none;
      display: flex;
      flex-wrap: wrap;
      padding-left: 96px;
      padding-right: 33px;
      margin-bottom: 38px;
    }

    &__visited-attractions-title {
      position: relative;
      margin-bottom: 43px;
      padding-right: 64px;


      &::before {
        content: "";
        position: absolute;
        width: 125px;
        height: 1px;
        background-color: #e5e5e5;
        top: 26px;
        left: -182px;
        }


        &::after {
          content: "";
          position: absolute;
          width: 125px;
          height: 1px;
          background-color: #e5e5e5;
          top: 26px;
          right: -122px;
          }
    }

    &__visited-attractions-label {
      width: 50%;
      padding: 0;
      margin-bottom: 32px;
      .font-values (18px;26px);

       &::after {
         left: 17px;
       }
    }

    &__feeling {
      width: 100%;
      order: 5;
      padding: 0;
      margin-bottom: 30px;
    }

    &__button-wrap {
      order: 6;
      padding: 0;
    }

    &__feeling-title {
      position: relative;
      margin-bottom: 60px;

      &::before {
        content: "";
        position: absolute;
        width: 125px;
        height: 1px;
        background-color: #e5e5e5;
        top: 13px;
        left: -194px;
        }


        &::after {
          content: "";
          position: absolute;
          width: 125px;
          height: 1px;
          background-color: #e5e5e5;
          top: 13px;
          right: -197px;
          }
     }

     &__feeling-review {
      .font-values (18px;26px);
      margin-bottom: 18px;
     }

     &__button {
      width: auto;
      font-kerning: none;
      margin-right: 154px;
      padding: 16px 75px 16px 64px;
     };

     &__footnote{
       display: inline-block;
       font-kerning: none;
     }


  }

}

// desktop

@media (min-width: @desktop-width) {

  .form {
    &__title {
      margin-bottom: 35px;
    }

    &__contents {
      padding: 0 379px;
    }

    &__impression {
      margin-left: 63px;
      width: 46%;

      &::before {
        right: 19px;
      }
    }

    &__impression-title {
      margin-left: 76px;
    }

    &__full-name {
      width: 43%;
    }

    &__contacts {
      display: flex;
      justify-content: center;
    }

    &__contacts-title{
       margin-bottom: 73px;
    }

    &__contacts-label {
      display: flex;
      align-items: center;
      margin-left: 17px;
      margin-right: 40px;
    }

    &__contacts-text {
      width: auto;
      margin-right: 16px;
      padding-top: 9px;
    }

    &__contacts-text--desktop-off {
      display: none;
    }

    &__input--contacts {
      flex-grow: 0;
      width: 257px;
    }

    &__contacts-title {
      &::before {
        width: 280px;
        left: -338px;
      }

      &::after {
        width: 280px;
        left: 338px;
      }
    }

    &__visited-attractions {
      display: flex;
      justify-content: space-between;
      padding: 0 62px;

    }

    &__visited-attractions-label {
      width: 21%;
    }

    &__visited-attractions-title {
       padding-right: 0;

        &::before {
          width: 280px;
          left: -338px;
        }

        &::after {
          width: 280px;
          left: 338px;
        }
      }

      &__feeling {
        padding: 0 60px;
      }

      &__feeling-title {
        &::before {
          width: 280px;
          left: -350px;
        }

        &::after {
          width: 280px;
          left: 328px;
        }
      }



      &__button-wrap {
          width: 100%;
          padding: 0 60px;
      }

      &__button {
        width: 50%;
        margin-right: 310px;
      }
}
}







