.copyright {
  display: flex;

  &__link {
  margin: 0 auto;
  text-decoration: none;
  display: flex;
  color: #000000;
  align-items: center;

  &:hover .copyright__logo {
    fill: @bg-blue;
  }

  &:active .copyright__logo {
    fill: rgba(0, 0, 0, 0.3);
  }
  }

  &__text {
    .font-values(14px; 26px);
    font-kerning: none;
    margin-right: 10px;
  }

  &__logo {
    width: 27px;
    height: 34px;
  }
}

