.mobile-page-style {
  width: 320px;
  margin: 0 auto;
}

.tablet-page-style {
  width: 768px;
  margin: 0 auto;
}

.desktop-page-style {
  width: 1200px;
  margin: 0 auto;
}

.ul-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.font-values(@font-size; @line-height) {
  font-size: @font-size;
  line-height:  @line-height;
}

.select {
  outline: 2px solid red;
}


