.page-header {
  .mobile-page-style();
  position: relative;
  margin-bottom: 40px;
}

.page-header__intro {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-position: top;
  position: relative;


  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-mobile@2x.jpg");
    background-size: auto 391px;
  }


  &::before {
  position: absolute;
  content: "";
  background-image: url("../img/bg-triangle-mobile.svg");
  bottom: -1px;
  left: 0px;
  z-index: 1;
  width: 100%;
  height: 26px;
}

  &__presentation {
    padding-bottom: 79px;
    margin-top: 90px;
    margin-left: 20px;
  }
}

.page-header__inner-img {

  background-image: url("../img/bg-back-mobile@1x.jpg");
  position: relative;
  background-position: bottom;

  @media (min-resolution: @retina-dppx) {

    background-image: url("../img/bg-back-mobile@2x.jpg");
    background-size: 246% 389px;
  }

  &::before {
    position: absolute;
  content: "";
  background-image: url("../img/bg-triangle-mobile.svg");
  bottom: -2px;
  left: 0px;
  z-index: 1;
  width: 100%;
  height: 26px;
  }
}

.page-header__inner-text {
 padding-bottom: 60px;
 text-align: center;
 margin-top: 53px;
 margin-left: 45px;
}

//  

@media (min-width: @tablet-width) {
 .page-header {
  .tablet-page-style();
  margin-bottom: 75px;

   &__intro {
     margin: 0 auto;
     background: url("../img/bg-back-tablet@1x.jpg") no-repeat 50%;
     position: relative;
     padding-bottom: 520px;

     @media (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
      background-size: auto 518px;
    }

     &::before {  //
      background-image: url("../img/bg-triangle-tablet.svg");
      width: 768px;
      height: 57px;
     }

     &__presentation {
       position: absolute;
       top: -11px;
       left: 134px;
       width: 460px;
       height: 350px;
     }
   }

   &__inner-img {
    margin: 0 auto;
     background-image: url("../img/bg-back-tablet@1x.jpg");
     background-size: 100% 518px;
     background-position: bottom;
     position: relative;
     padding-bottom: 292px;

     @media (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
      background-size: auto 518px;
    }

    &::before {
      position: absolute;
    content: "";
    background-image: url("../img/bg-triangle-tablet.svg");
    background-position: 50%;
    bottom: -1px;
    left: 0px;
    z-index: 15;
    width: 768px;
    height: 57px;
    }
  }

  &__inner-text {
  position: absolute;
  width: 467px;
  height: 79px;
  top: 43px;
  left: 107px;
  }
 }
}


//  
@media (min-width: @desktop-width) {
.page-header {
  .desktop-page-style;
  margin-bottom: 108px;

  &__intro {
    margin: 0 auto;
    background: url("../img/bg-back-desktop@1x.jpg") no-repeat 50%;
    position: relative;
    padding-bottom: 520px;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-back-desktop@2x.jpg");
      background-size: auto 518px;
    }

    &::before {  //
     background-image: url("../img/bg-triangle-desktop.svg");
     width: 1200px;
     height: 57px;
     left: 0;
     }

    &__presentation {
      left: 350px;
    }
  }

  &__inner-img {
    background-image: url("../img/bg-back-desktop@1x.jpg");

    padding-bottom: 302px;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-back-desktop@2x.jpg");
      background-size: auto 518px;
    }

   &::before {    //
    background-image: url("../img/bg-triangle-desktop.svg");
    width: 1200px;
    height: 57px;
    left: 0;
 }
}

  &__inner-text {

   width: 461px;
   height: 80px;
   top: 50px;
   left: 326px;
  }
}


}
