.booking {
  .mobile-page-style();
  text-align: center;

  &__title {
    font-size: 21px;
    font-weight: bold;
    margin: 0;
    margin-bottom: 21px;
  }

  &__info {
    margin: 0;
    margin-bottom: 35px;
    .font-values(14px; 24px);
    padding: 0 20px 0 20px;
    color: #333333;

  }

  &__button {
    padding: 16px 46px 16px 46px;
    margin-left: 6%;
    margin-right: 6%;
    margin-bottom: 50px;
  }

  &__maps {
    border: none;
    height: 350px;
    width: 100%;
  }

}


// tablet version

@media (min-width: @tablet-width) {
  .booking {
    .tablet-page-style();
margin-bottom: 80px;

   &__title {
     .font-values(30px;26px);
     margin-top: 50px;
     margin-bottom: 33px;
   }

   &__info {
     padding-left: 167px / 768px * 100%;
     padding-right: 167px / 768px * 100%;
     margin-bottom: 40px;
   }

   &__button {
     width: 458px;
   }
  }
}

// desktop version

@media (min-width: @desktop-width) {
  .booking {
  .desktop-page-style();

  &__title {
    margin-top: 55px;
  }

  &__info {
    padding-left: 383px / 1280px * 100%;
    padding-right: 383px / 1280px * 100%;
  }
 }
}


