.intro-photo {
  text-align: center;

  &__title {
    padding: 0px 30px 25px 30px;
    font-kerning: none;
  }

  &__contents {
    padding: 0 24px 27px 24px;
    font-kerning: none;
    line-height: 24px;
  }
}

 .photo {
  text-align: center;
  margin-bottom: 30px;

  &__reviews-list {
    .font-values(14px;24px);
  }

  &__reviews-item {
    padding: 22px 20px 17px 20px;
    border-top: solid 1px #e5e5e5;
    text-align: left;
  }

  &__reviews-item:last-child {
    border-bottom: solid 1px #e5e5e5;
  }

  &__reviews-photo {
    margin-bottom: 8px;
  }

  &__reviews-title {
    font-weight: bold;
  }

  &__reviews-author {
    text-transform: none;
     margin: 0;
     margin-bottom: 8px;
  }

  &__reviews-number-wrap{
   position: relative;
  }

  &__icon-like {
    position: absolute;
    fill: #000000;
    opacity: 0.3;
    width: 23px;
    height: 21px;
    top: -1px;
    left: 0px;
  }

  &__reviews-like {
    font-weight: bold;
    margin: 0;
    margin-left: 37px;
   }
}

// tablet-version

@media (min-width: @tablet-width) {

  .intro-photo{

    &__title {
      padding: 0px 20% 30px 20%;
    }

    &__contents {
      padding: 0px 22% 71px 22%;
    }
   }

    .photo {
    margin-bottom: 68px;
    padding: 0 60px;
    box-sizing: border-box;
    font-kerning: none;

    &__reviews-list {
      display: flex;
      flex-wrap: wrap;
    }

    &__reviews-item {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 50%;
      border: none;
      padding: 0px;
    }

    &__reviews-item:nth-child(even) {
      background-color:#f2f2f2 ;
    }

    &__reviews-item:nth-child(odd) {
      background-color: #eaeaea;
    }

    &__reviews-photo {
      margin-bottom: 7px;
    }

    &__reviews-item--first {
      width: 100%;
      background-color: #f2f2f2;
    }

    &__reviews-item--first .photo__reviews-photo {
      width: 648px;
      height: 400px;
    }

    &__reviews-description {
      display: flex;
      justify-content: space-between;
      width: 100%;
    }

    &__reviews-text-wrap {
      padding-left: 20px;
    }


    &__reviews-author {
      margin-bottom: 17px;
    }

    &__reviews-photo {
      width: 324px;
      height: 267px;
    }

    &__reviews-item:last-child {
      border-bottom: none;
    }

    &__reviews-number-wrap {
      width: 53px;
    }

    &__icon-like {
      top: 11px;
      left: -32px;
    }

    &__reviews-like {
     margin: 0;
     padding-top: 12px;

    }
  }

}

// desktop-version

@media (min-width: @desktop-width) {

  .photo {
    margin-bottom: 71px;
    padding: 0;
  &__reviews-item--first .photo__reviews-photo {
    width: 1200px;
  }

  &__reviews-item {
    width: 33.3333%;
    position: relative;
    color: #ffffff;
  }

  &__reviews-item--first{
    width: 100%;
  }

  &__reviews-photo {
    width: 400px;
    margin-bottom: -8px;
  }

  &__reviews-description{
    position: absolute;
    bottom: -2px;
    left: 1px;
    background-color: rgba(0, 0, 0, 0.3);
    padding-top: 15px;

  }

  &__icon-like {
    opacity: 1;
    fill: @bg-white;
    top: 11px;
    left: -32px;
  }
 }
}

