.city-offer {
  width: 210px;
  margin: 0 auto;
  text-align: left;

  &__list {
  .ul-list();
  color: #000000;
  margin-top: 35px;
  background-color: @bg-white;
  margin-bottom: 38px;
  }

  &__item {
    margin-left: 15px;
    position: relative;
  }

  &__item:not(:last-child) {
    margin-bottom: 28px;
  }

  &__intro {
   margin: 0;
   font: inherit;
   font-weight: bold;
   margin-bottom: 15px;
  }

  &__info {
    font-size: 14px;
    line-height: 21px;
    margin: 0;
  }

  &__item--lodging::before {
    position: absolute;
    content: "";
    width: 39px;
    height: 37px;
    background-image: url(../img/icon-house.svg);
    background-size: 39px 37px;
    left: -49px;
    top: -7px;

  }

  &__item--souvenirs::before {
    position: absolute;
    content: "";
    width: 32px;
    height: 38px;
    background-image: url(../img/icon-gift.svg);
    background-size: 32px 38px;
    left: -46px;
    top: -7px;
  }

  &__item--food::before {
    position: absolute;
    content: "";
    width: 40px;
    height: 35px;
    background-image: url(../img/icon-burger.svg);
    background-size: 40px 35px;
    left: -53px;
    top: -2px;
  }
}



// tablet-version

@media (min-width: @tablet-width) {
.city-offer {
  .tablet-page-style();

		&__list {
   display: flex;
   margin-bottom: 33px;
    }

		&__item {
      width: 33.33%;
      .font-values(21px;26px);
      margin: 0;
      box-sizing: border-box;
      padding: 122px 45px 0 45px;
		}

		&__item--lodging {
      order: 1;

      &::before {
        width: 75px;
        height: 72px;
        background-size: 75px 72px;
        left: 90px;
        top: 24px;
      }
		}

		&__intro {
      text-align: center;
      margin-bottom: 23px;
		}

		&__info {
      text-align: center;
      font-kerning: none;
     }

		&__item--souvenirs {
      order: 3;

      &::before {
      width: 64px;
      height: 75px;
      background-size: 64px 75px;
      left: 96px;
      top: 21px;
     }
		}

		&__item--food {
      order: 2;

      &::before {
        width: 77px;
        height: 68px;
        background-size: 77px 68px;
        left: 89px;
        top: 28px;
       }
    }
}
.city-offer--bg {
  background-color: @bg-white;
  }
}


// desktop version
@media (min-width: @desktop-width) {
.city-offer {
  .desktop-page-style;
  &__list {
    margin-bottom: 55px;
     }

  &__info {
    padding-left: 10px;
    padding-right: 10px;
  }

  &__item--lodging::before {
      left: 162px;
  }

  &__item--souvenirs::before {
    left: 168px;
  }

  &__item--food::before {
    left: 161px;
  }
 }
}
