
  .modal-body {
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 45px 55px;
    box-sizing: border-box;
    opacity: 0;
    transition: all 0.3s ease 0s;
    transform: translate(0px, -100%);



    &__content {
    .mobile-page-style();
    padding: 35px 0;
    text-align: center;
    background-color: #ffffff;
    box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
    }

   &__title {
    text-align: center;
    margin-bottom: 25px;
    }

   &__text {
    text-align: center;
    margin-bottom: 20px;
    padding: 0 50px;

    }

   &__button-wrapper {
    .mobile-page-style();
    background-color: #f2f2f2;
    text-align: center;
    padding: 20px 0;
    margin-top: -20px;
    box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
    z-index: 30;

   }

   &__button-close {
    .font-values(18px;26px);
     border: none;
     padding: 15px 75px;
  }

 }

// Tablet-version

@media (min-width: @tablet-width) {
.modal-body {

  &__title--break {
    padding-left: 70px;
    padding-right: 70px;
    line-height: 36px;
  }

  &__button-wrapper {
    width: 640px;
  }

  &__button-close{
    .font-values(21px;26px);
    padding: 15px 155px;
  }
}
}
