

.advantages {
  .mobile-page-style();
     text-align: center;
     margin-bottom: 27px;

  &__list {
    position: relative;
    .ul-list();
  }

  &__item {
    color: @bg-white;
    background-color: @bg-blue;
  }
  &--gray{
    color: #000000;
    background-color: @bg-gray;
  }

  &__item-wrap {
    box-sizing: border-box;
    font-kerning: none;
    padding: 40px 27px 40px 27px;
  }

  &__slogan {
    display: block;
    margin-bottom: 9px;
   }

  &__description {
    .font-values(14px;21px);
    margin: 0;

  }

  &__number {
    display: block;
    margin-bottom: 15px;
  }

  &__photo {
    margin-bottom: -7px;
  }
}

.advantages__item--gray {
  position: relative;
  background-color: @bg-gray;
  color: #000000;
  padding: 27px 32px 28px 32px;

 &:not(:last-child)::before {
  position: absolute;
  content: "";
  width: 280px;
  height: 1px;
  background-color: #d6d6d6;
  bottom: 2px;
  left: 20px;

 }

  & .advantages__slogan {
    margin-bottom: 10px;
}

}


// tablet-version

@media (min-width: @tablet-width) {
  .advantages {
    .tablet-page-style();

		&__item {
      display: flex;

      &-wrap {
        display: flex;
        flex-direction: column;
        width: 33%;
        text-align: center;
        height: 256px;
        padding: 45px 45px 40px 45px;
      }
       &-img {
         width: 67%;
         height: 256px;
         position: relative;
         overflow: hidden;
        }
     }

		&__slogan {
      .font-values(21px;26px);
      margin-bottom: 10px;

    }

		&__number {
      margin-bottom: 22px;
		}

		&__photo {
      height: 256px;
      width: 100%;
      position: absolute;
      top: 0px;
      left: 0px;
  }


    &__item--reverse{
    flex-direction: row-reverse;
    .advantages__slogan {
      padding-left: 6px;
      padding-right: 6px;
    }
   }

   &__list-wrap {
    display: flex;

  }

  &__item--gray {
    width: 33.33%;
    flex-direction: column;
    padding: 45px 49px 49px 49px;

    &:not(:last-child)::before {
      content: none;

     }
    }
   }
  }

// desktop

@media (min-width: @desktop-width) {

  .advantages {
    .desktop-page-style;

    &__photo {
      height: 400px;
      width: 100%;
      left: 0px;
      top: -72px;
    }

    &__item-wrap {
      padding: 50px 65px 63px 65px;
    }

    &__slogan {
      padding-left: 72px;
      padding-right: 72px;
    }

    &__item--gray {
      padding-bottom: 65px;
    }
  }

  .advantages__item--reverse {
    .advantages__slogan {
      padding-left: 56px;
      padding-right: 58px;
    }
  }
}
