.video-player {
  position: relative;

  &::before {
    position: absolute;
    content: "";
     width: 9px;
     height: 9px;
     background-color: #ffffff;
     bottom: 17px;
     left: 77px;
     border-radius: 50%;
     z-index: 1;
  }

  &__status-bar {
    position: absolute;
    width: 280px;
    height: 45px;
    opacity: 0.6;
    bottom: 0;
    left: 20px;
    background-color: #000;

    &::after {
      position: absolute;
      content: "";
      width: 235px;
      height: 3px;
      background-color: @bg-white;
      opacity: 0.2;
      top: 22px;
      left: 23px;
    }

  }

  &__btn-wrap {
    display: flex;
    position: absolute;
    background-color: #f2f2f2;
    bottom: -66px;
    left: 20px;
    width: 280px;
  }

  &__button {
    border: none;
    margin-bottom: 18px;
    margin-top: 22px;
    margin-right: 18px;
    background-color: transparent;
  }

  &__button--replay {
      margin-left: 19px;
  }

  &__button--subtitles {
    margin-left: auto;

  }

  &__icon {
    fill: #5c6870;

    &:hover {
      fill: #333333;
    }
    &:active {
      fill: #333333;
      opacity: 0.3;
    }
  }
}


// tablet-verdion

@media (min-width: @tablet-width) {
  .video-player {
    &::before {
      bottom: 41px;
      left: 207px;
    }

    &__status-bar {
      width: 100%;
      height: 93px;
      left: 0;

      &::after {
        width: 389px;
        top: 46px;
        left: 91px;
      }
    }

    &__btn-wrap {
      width: 100%;
      background-color: transparent;
      bottom: 0;
      left: 0;
      z-index: 1;
    }

    &__button {
      margin-bottom: 32px;
    }

    &__button--replay {
      margin-left: 30px;
    }

    &__button--fullscrean {
      margin-right: 35px;
    }

    &__icon {
    fill: #ffffff;

    &:hover {
      fill: #ffffff;
      opacity: 0.6;
    }

    &:active {
      fill: #ffffff;
      opacity: 0.3;
    }
  }
 }
}

