

  .intro {
    position: relative;
    .mobile-page-style();
    text-align: center;

    &::before {
     position: absolute;
     content: "";
     width: 100%;
     height: 37px;
     background-image: url("../img/big-triangle-new.svg");
     bottom: -1px;
     left: 0px;
     background-color: @bg-blue;
     z-index: 1;

    }

   &__title{
   padding: 0px 30px 28px 30px;
   .font-values(18px; 26px);
    font-weight: bold;
    margin: 0;
   }

   &__contents{
    padding: 0 30px 43px 30px;
    .font-values(14px; 26px);
    margin: 0;
    color: #333333;
   }


}

// tablet-version, desktop


@media (min-width: @tablet-width) {

  .intro {
    .tablet-page-style();
    font-kerning: none;
    &::before{
      content: none;
    }

		&__title {
      .font-values(21px;26px);
      padding: 0px 20% 30px 20%;

		}

		&__contents {
      padding-bottom: 50px;
		}
}


}
