.site-list a{
  display: block;
  text-decoration: none;
  color: inherit;

  text-align: center;
  line-height: 40px;
  padding: 7px 10px;
}

.site-list {


  &__item:not(.site-list__item-logo) {
  background-color: @bg-blue;


    &:not(:first-child) {
    border-top: 1px solid #9ac2db;
    }

    &:not(:last-child) {
    border-bottom: 1px solid #678fa8;
    }

    &:last-child {
      box-shadow: inset 0 -2px 5px #6d97b1;
    }

    &:hover {
      background-color: @bg-blue-hover;
    }

    &:active {
      background-color: @bg-blue-active;
    }

    & a[href]:active {
      opacity: 0.3;
    }

}

&__item-logo a {
  padding: 0;
}
}

// tablet-version

@media (min-width: @tablet-width) {
.site-list {

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &__item-logo{
    margin: 0 auto;
  }

  &__item:not(.site-list__item-logo) {
    color: #000;
    .font-values(14px;26px);
    font-kerning: none;
    font-weight: bold;
    background-color: @bg-white;
    width: 20%;


    &:last-child {
    box-shadow: none;
    }

    &:not(:first-child) {
    border-top: none;
    }

    &:not(:last-child) {
    border-bottom: none;
    }

    &:hover {
      background-color: @bg-white;
    }

    &:active {
      background-color: @bg-white;
    }

    & a[href]:hover {
      color: @bg-blue;
    }

    & a[href]:active {
      color: rgba(0 ,0 ,0 , 0.3);
    }
  }

  &__item--active a{
    color: #766357;
    cursor: pointer;
  }

  &__item--active a:hover{
    color: #604e43;
  }

  &__item--active a:active{
    color: rgba(118, 99, 87, 0.3);
  }
 }
}


